import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Link} from "react-router-dom";
import NumberFormat from "react-number-format";
import moment from "moment";
import ReactMomentCountDown from 'react-moment-countdown'
import { Typography, List,Card,Skeleton,Badge,Tooltip, Button,Collapse,Avatar } from 'antd'
import Flex from "../../../../components/shared-components/Flex";
import {ShoppingOutlined,EyeOutlined,FileTextOutlined} from "@ant-design/icons";
import Utils from '../../../../utils/index'


const TransactionUnpaid = (props)=> {
    const Extra = (item) => {
        return (
            <Flex>
                <Tooltip title={'Detail'}>
                    <Button
                        ghost
                        className={'mx-2'}
                        type={'primary'}
                        htmlType={'button'}
                        title={'Detail'}
                        size={'small'}
                        href={`/transaction/thank-you/${item?.id}?invoice_number=${item?.invoice?.invoice_number ?? ""}`}
                        icon={<EyeOutlined/>}
                    />
                </Tooltip>
                <Tooltip title={'Invoice Detail'}>
                    <Button
                        ghost
                        className={'mx-2'}
                        type={'primary'}
                        htmlType={'button'}
                        title={'Detail'}
                        size={'small'}
                        href={`/invoice/p/${item?.id}?invoice_number=${item?.invoice?.invoice_number ?? ""}`}
                        icon={<FileTextOutlined />}
                    />
                </Tooltip>


            </Flex>
        )
    }
    return (
        <div className="w-full">
            {[0,1,2,3,4].map((item,index)=> {
                return (
                    <Card
                        loading={false}
                        title={<Flex alignItems={'center'} mobileFlex={true} justifyContent={'between'} key={`book-${index}`}>
                                <div className={''}>
                                    <div>
                                        <Typography.Text className={'mr-2'}>Booking
                                            Date</Typography.Text>
                                    </div>
                                    <div>
                                        <Typography.Title level={4} style={{
                                            margin: 0,
                                            padding: 0,
                                            fontSize: '14px'
                                        }}>{moment().add(2,'day').format("YYYY MMM DD, HH:mm a")}</Typography.Title>
                                    </div>
                                </div>
                                <Flex alignItems={'center'} mobileFlex={true} key={`book-${index}`}>
                                    <ShoppingOutlined style={{margin: 0, padding: 0}}/>
                                    <Typography.Title level={2}
                                                      style={{fontSize: "16px", margin: "0 5px"}}>Booking</Typography.Title>
                                </Flex>

                            </Flex>}
                        extra={<Extra {...item}/>}>
                        <List
                            className="demo-loadmore-list mb-2"
                            loading={false}
                            itemLayout="horizontal"
                            dataSource={[{...item}]}
                            renderItem={_ => {
                                return (
                                    <List.Item>
                                        <Skeleton avatar title={false} loading={_.loading} active>
                                            <div style={{width: '100%'}}>
                                                <List.Item.Meta
                                                    avatar={<Avatar
                                                        src={null}
                                                    />
                                                }
                                                    title={
                                                        <Flex alignItems={'center'}>
                                                            <Link to={`/transaction/detail/`} title={'detail'}
                                                                  className={'mr-2'}>{"Room Name" ?? "-"}</Link>
                                                        </Flex>
                                                    }
                                                    description={
                                                        <div>
                                                            <Badge color={'orange'} text={'status'}/>
                                                        </div>
                                                    }
                                                />
                                            </div>
                                            <div style={{width: "100%"}}>
                                                <Flex alignItems={'center'} mobileFlex={false}
                                                      justifyContent={'between'} style={{width: "100%"}}>
                                                    <div className={''}>
                                                        <div>
                                                            <Typography.Text className={'mr-2'}>Booking
                                                                Date</Typography.Text>
                                                        </div>
                                                        <div>
                                                            <Typography.Title level={4} style={{
                                                                margin: 0,
                                                                padding: 0,
                                                                fontSize: '14px'
                                                            }}>{moment().add(2,'day').format("YYYY MMM DD, HH:mm a")}</Typography.Title>
                                                        </div>
                                                    </div>
                                                    <div>
                                                        <div>
                                                            <Typography.Text className={'mr-2'}>ORDER
                                                                #{31512512985 ?? "-"}</Typography.Text>
                                                        </div>
                                                        <div>
                                                            <Typography.Text
                                                                className={'mr-2'}>Rp. {Utils.numberFormat(200000, ',') ?? "-"}</Typography.Text>
                                                        </div>


                                                    </div>
                                                </Flex>
                                            </div>

                                        </Skeleton>
                                    </List.Item>
                                )
                            }}
                        />
                        <Collapse defaultActiveKey={[]}>
                            <Collapse.Panel key={1} header={"Virtual Account Number"}>
                                <Flex alignItems={'center'} mobileFlex={false} justifyContent={'between'}
                                      className={'py-4'}>
                                    <Flex alignItems={'center'} mobileFlex={true}>
                                        <img src={'/img/bank/icon-mandiri.png'}/>
                                        <div className={'mx-2'}>
                                            <Typography.Text>Payment Method</Typography.Text>
                                            <Typography.Title level={4} style={{margin: 0}}>Mandiri Virtual
                                                Account</Typography.Title>
                                        </div>
                                    </Flex>
                                    <Flex alignItems={'center'} mobileFlex={true}>
                                        <div className={'mx-2'}>
                                            <Typography.Text>Virtual Account Number</Typography.Text>
                                            <Typography.Title level={4}
                                                              style={{margin: 0}}>{item?.payment_code ?? "8870885695190268"}</Typography.Title>
                                        </div>
                                    </Flex>

                                    <Flex alignItems={'center'} mobileFlex={true}>
                                        <div className={'mx-2'}>
                                            <Typography.Text>Expired Date</Typography.Text>
                                            <p>
                                                <ReactMomentCountDown
                                                    // sourceFormatMask='YYYY-MM-DD HH:mm:ss'
                                                    toDate={moment().add(2,'hour').utc()}
                                                    // toDate={moment(item?.invoice?.invoice_due_date).utc(7).format("YYYY-MM-DD HH:mm:ss")}
                                                    onTick={()=> {}}
                                                    onCountdownEnd={()=> (
                                                        <Typography.Text danger>Expired</Typography.Text>
                                                    )}/>
                                            </p>
                                        </div>
                                    </Flex>

                                    <Flex alignItems={'center'} mobileFlex={true}>
                                        <div className={'mx-2'}>
                                            <Typography.Text>Total Payment</Typography.Text>
                                            <Typography.Title level={4} style={{margin: 0}}>
                                                <NumberFormat
                                                    displayType={'text'}
                                                    value={(1251295).toFixed(2)}
                                                    prefix={'Rp '}
                                                    thousandSeparator={true}
                                                />
                                            </Typography.Title>
                                        </div>
                                    </Flex>

                                </Flex>
                            </Collapse.Panel>
                        </Collapse>

                    </Card>
                )
            })}
        </div>
    )
}

TransactionUnpaid.propTypes = {}
TransactionUnpaid.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(TransactionUnpaid))