import React, {useState} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Container from "../../../components/layout-components/container";
import {Card, Tabs} from "antd";
import {useLocation} from "react-router-dom";
import LayoutContent from "../../../components/layout-components/LayoutContent";
import TransactionUnpaid from "./transaction-unpaid";
import TransactionPaid from "./transaction-paid";
import TransactionCanceled from "./transaction-canceled";
import TransactionExpired from "./transaction-expired";

const Transaction = (props)=> {

    const locations = useLocation()
    const [tabsActive, setTabsActive] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "unpaid"
    })
    const [tabs] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "unpaid"
    })
    function setUp(value) {
        if (window) {

            const urlSearch = new URLSearchParams(window.location.search)
            if(urlSearch.get('tab')){
                urlSearch.delete('tab')
                urlSearch.append('tab', value)
            }else{
                urlSearch.append('tab', value)
            }
            let query = []
            urlSearch.forEach((value, key) => {
                query.push(`${key}=${value}`)
            })

            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, query.join("&")].join("?"));

        }
    }
    function onTabs(value) {
        setTabsActive(value)

        setUp(value)
    }

    return (
        <LayoutContent style={{marginTop: 120, minHeight: "calc(100vh)"}}>
            <div className="w-full py-4">
                <Container size={'default'}>
                    <Card bordered={false} title={'Transaction'}>
                        <Tabs defaultActiveKey={tabs} onChange={onTabs}>
                            <Tabs.TabPane key={'unpaid'} tab={'Unpaid'}>
                                <TransactionUnpaid/>
                            </Tabs.TabPane>
                            <Tabs.TabPane key={'paid'} tab={'Paid'}>
                                <TransactionPaid/>
                            </Tabs.TabPane>
                            <Tabs.TabPane key={'canceled'} tab={'Canceled'}>
                                <TransactionCanceled/>
                            </Tabs.TabPane>
                            <Tabs.TabPane key={'expired'} tab={'Expired'}>
                                <TransactionExpired/>
                            </Tabs.TabPane>
                        </Tabs>
                    </Card>
                </Container>
            </div>
        </LayoutContent>
    )
}

Transaction.propTypes = {}
Transaction.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(Transaction))